


















































import {
  deleteTypeConvert,
  getTypeConversions,
  getTypes,
} from "@/api/apis/type.api";
import { rules } from "@/constants";
import Vue from "vue";
import TableHeader from "../core/TableHeader.vue";
import AddConvert from "@/components/type/AddConvert.vue";
import { ConfirmDialog, TableHeaderModel } from "../models";
import { mapMutations, mapState } from "vuex";
import { DIALOG } from "@/store/store_types";
export default Vue.extend({
  components: { TableHeader, AddConvert },
  props: ["type"],
  data: () => ({
    rules: rules,
    search: "",
    isLoading: true,
    headers: [
      {
        text: "از نوع",
        align: "start",
        sortable: false,
        value: "fromType.name",
      },
      {
        text: "به نوع",
        align: "start",
        sortable: false,
        value: "toType.name",
      },
      {
        text: "از مقدار",
        align: "start",
        sortable: false,
        value: "fromValue",
      },
      {
        text: "به مقدار",
        align: "start",
        sortable: false,
        value: "toValue",
      },
      {
        text: "",
        align: "start",
        sortable: false,
        value: "actions",
      },
    ] as Array<TableHeaderModel>,
    conversions: [] as Array<any>,
    newConvert: false,
  }),
  computed: {
    ...mapState(DIALOG, {
      dialogResult: `dialogResult`,
      confirmDialogResult: `confirmDialogResult`,
    }),
  },
  watch: {
    dialogResult(result) {
      if (result != undefined && result.status) {
        this.conversions.push(result.data);
        this.newConvert = false;
      }
    },
  },
  created() {
    this.loadConversions();
  },
  methods: {
    loadConversions() {
      this.isLoading = true;
      getTypeConversions(this.type.id)
        .then((convertRes) => {
          if (convertRes.status)
            this.conversions = convertRes.result.conversions;
        })
        .finally(() => (this.isLoading = false));
    },
    addConvert() {
      this.newConvert = !this.newConvert;
    },
    removeConvert(item: any) {
      deleteTypeConvert(item.id).then((delRes) => {
        if (delRes.status) {
          let index = this.conversions.findIndex((c) => c.id == item.id);
          if (index != -1) this.conversions.splice(index, 1);
        }
      });
    },
  },
});
